import React, { Component } from "react";
import "../../../App.css";
class ProductionRunListNotifications extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="plusNotificationBar">
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>

        {showNotification ? (
          <div class="to-Patching-list-data">
            <div class="container">
              <div class="row">
                <div class="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div class="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div class="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div class="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <a href="main-dashboard.html">
                          <i class="fa fa-home"></i>
                        </a>
                      </li>
                      <li class="iconModuleClose">
                        <i onClick={this.OnShowClick} class="fa fa-times"></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div class="row mt-2 mb-2">
                <div class="col-sm-12">
                  <ul className="to-Purchasing-action-menu-1-list">
                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-left fa-2x"></i>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">3</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">2</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>

                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-right fa-2x"></i>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>

              <div class="row mt-3">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Patching-list-data-box">
                    <a href="/#">Add New</a>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6">
                  <div class="to-Patching-list-data-box">
                    <a href="/#">Edit</a>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Patching-list-data-box">
                    <a href="/#">Delete</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : null}
      </div>
    );
  }
}

export default ProductionRunListNotifications;
