import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

//import components
import Dashboard from "./layouts/Dashboard";
import Users from "./layouts/Users";
import AuditLogs from "./layouts/AuditLogs";
import ErrorLogs from "./layouts/ErrorLogs";
import Permissions from "./layouts/Permissions";
import UserDetails from "./layouts/UserDetails";
import UserPermissions from "./layouts/UserPermissions";
import UserRoles from "./layouts/UserRoles";
import UserSettings from "./layouts/UserSettings";

// import User from './layouts/User';

//import assets
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "./App.css";
import "../src/assets/css/responsive.css";
import CuttingsDashboard from "./layouts/Cuttings/CuttingsDashboard";
import Demand from "./layouts/Cuttings/Demand";
import ProductionRunList from "./layouts/Cuttings/ProductionRunList";
import TransplantingPlannig from "./layouts/Cuttings/TransplantingPlannig";
import SeedingDashboard from "./layouts/Seeding/SeedingDashboard";
import PatchingDashboard from "./layouts/Patching/PatchingDashboard";
import PatchingList from "./layouts/Patching/PatchingList";
import PatchingProduction from "./layouts/Patching/PatchingProduction";
import ShortageExtras from "./layouts/Patching/ShortageExtras";
import LogisticsDashboard from "./layouts/Logistics/LogisticsDashboard";
import LogisticsRouting from "./layouts/Logistics/LogisticsRouting";
import Routes from "./layouts/Logistics/Routes";
import Shipments from "./layouts/Logistics/Shipments";
import CuttingsProduction from "./layouts/Cuttings/CuttingsProduction";

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route path="/" exact component={Dashboard} />
          <Route path="/users" component={Users} />
          <Route path="/audit_logs" component={AuditLogs} />
          <Route path="/error_logs" component={ErrorLogs} />
          <Route path="/permissions" component={Permissions} />
          <Route path="/user_details" component={UserDetails} />
          <Route path="/user_permissions" component={UserPermissions} />
          <Route path="/user_roles" component={UserRoles} />
          <Route path="/user_settings" component={UserSettings} />
          <Route path="/cuttings/dashboard" component={CuttingsDashboard} />
          <Route path="/cuttings/demand" component={Demand} />
          <Route path="/cuttings/production" component={CuttingsProduction} />

          <Route
            path="/cuttings/production_run_list"
            component={ProductionRunList}
          />
          <Route
            path="/cuttings/transplanting_planning"
            component={TransplantingPlannig}
          />
          <Route
            path="/seeding/seeding_dashboard"
            component={SeedingDashboard}
          />
          <Route
            path="/cuttings/production_run_list"
            component={ProductionRunList}
          />
          <Route
            path="/cuttings/transplanting_planning"
            component={TransplantingPlannig}
          />
          <Route path="/patching/dashboard" component={PatchingDashboard} />
          <Route path="/patching/patching_list" component={PatchingList} />
          <Route
            path="/patching/patching_production"
            component={PatchingProduction}
          />
          <Route path="/patching/shortages_extras" component={ShortageExtras} />
          <Route path="/logistics/dashboard" component={LogisticsDashboard} />
          <Route
            path="/logistics/logistics_routing"
            component={LogisticsRouting}
          />
          <Route path="/logistics/routes" component={Routes} />
          <Route path="/logistics/shipments" component={Shipments} />
          <Route path="/seeding/dashboard" component={SeedingDashboard} />
        </Switch>
      </Router>
    );
  }
}

export default App;
