import React, { Component } from "react";
import { Link } from 'react-router-dom';
import "../App.css";

class PermissionButtons extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container">
            <div className="d-flex justify-content-lg-end justify-content-md-end justify-content-sm-start ">
              <a
                href="/#"
                className="button-default to-button-default button-dark"
              >
                Edit
              </a>

              <a
                href="/#"
                className="button-default to-button-default button-dark"
              >
                Delete
              </a>

              <Link to="/user_permissions" className="button-default to-button-default ">
                Add
              </Link>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default PermissionButtons;
