import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../App.css";

class RecentUsedModule extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="recent-title text-center">
              <h3>Recently Used</h3>
            </div>
          </div>
        </div>

        <div className="row recent-used-item">
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img
                src={require("../assets/icons/icon_purchasing_main.png")}
                alt=""
              />
              <span>Purchasing</span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>
                <Link to="/patching/dashboard">Patching</Link>
              </span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>
                <Link to="/cuttings/dashboard">Cuttings</Link>
              </span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>Transplanting</span>
            </div>
          </div>
        </div>

        <div className="row recent-used-item">
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>Forecasting</span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>Shipping</span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>Quality</span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/tint.png")} alt="" />
              <span>Growing</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RecentUsedModule;
