import React, { Component } from "react";
import "../../App.css";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ProductionRunListTable from "./Components/ProductionRunListTable";
import NewRunListTable from "./Components/NewRunListTable";
import CopyRightsText from "../../components/CopyRightsText";
import ProductionRunListNotifications from "./Components/ProductionRunListNotifications";

class ProductionRunList extends Component {
  componentDidMount() {
    document.title = "Production Run List";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Production Run List" />
        <SearchFilterArea />
        <ProductionRunListTable />
        <div class="row">
          <div class="col-sm-12">
            <div class="table-heading text-center">
              <h1>New Runlist</h1>
            </div>
          </div>
        </div>
        <NewRunListTable />
        <ProductionRunListNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default ProductionRunList;
