import React, { Component } from "react";
import "../../../App.css";
class TransplantingForm extends Component {
  render() {
    return (
      <div>
        <div class="container-fluid ">
          <form class="es_purchase-input">
            <div class="form-row">
              <div class="form-group col-md-3">
                <label class="to-title-f-p-size" for="inputTerms">
                  Items Sticked
                </label>
                <select class="form-control to-input-f-b-c to-classic">
                  <option selected>...</option>
                  <option>...</option>
                </select>
              </div>
              <div class="form-group col-md-3">
                <div class="row">
                  <div class="col">
                    <label
                      class="to-title-f-p-size to-hide"
                      for="inputPurchaseOrder"
                    >
                      Qty
                    </label>
                    <input
                      type="text"
                      class="form-control to-input-f-b-c"
                      id="inputPurchaseOrder"
                      placeholder="qty"
                    />
                  </div>

                  <div class="col text-center">
                    <label
                      class="to-title-f-p-size to-label-top"
                      for="inputDept"
                    >
                      Transplanting
                    </label>
                  </div>
                </div>
              </div>
              <div class="form-group col-md-3">
                <label class="to-title-f-p-size" for="inputTerms">
                  Dropdown List
                </label>
                <select class="form-control to-input-f-b-c to-classic">
                  <option selected>...</option>
                  <option>...</option>
                </select>
              </div>
              <div class="form-group col-md-3">
                <label class="to-title-f-p-size to-hide" for="inputCreator">
                  Qty
                </label>
                <input
                  type="text"
                  class="form-control to-input-f-b-c"
                  id="inputCreator"
                  placeholder="qty"
                />
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-4 offset-md-1">
                <div class="row">
                  <div class="col">
                    <label
                      class="to-title-f-p-size to-hide"
                      for="inputPurchaseOrder"
                    >
                      Location
                    </label>
                    <input
                      type="text"
                      class="form-control to-input-f-b-c"
                      id="inputPurchaseOrder"
                      placeholder="location"
                    />
                  </div>
                </div>
              </div>
              <div class="form-group text-center col-md-3 col-lg-2">
                <label class="to-title-f-p-size to-label-top" for="inputDept">
                  Transplanting To
                </label>
              </div>
              <div class="form-group col-md-4">
                <label class="to-title-f-p-size to-hide" for="inputTerms">
                  Location
                </label>
                <input
                  type="text"
                  class="form-control to-input-f-b-c "
                  id="inputDept"
                  placeholder="location"
                />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default TransplantingForm;
